






































































import { onPullDownRefresh, onReady } from "@dcloudio/uni-app";
import { ref } from "vue";
import { useCool } from "/@/cool";
import { useChat } from "/@/hooks";

const { ui, router, service } = useCool();

// 聊天
const { friend } = useChat();

// 弹出菜单
const ActionSheet = ref<ClActionSheet.Ref>();

// 用户信息
const info = ref<any>();

// 刷新
async function refresh() {
	ui.showLoading();

	await service.chat.friend
		.info({
			id: router.query.id,
		})
		.then((res) => {
			info.value = res;
		});

	ui.hideLoading();
}

// 添加
async function add() {
	ui.showLoading();

	await service.chat.friend
		.add({
			id: router.query.id,
		})
		.then(() => {
			ui.showTips("成功添加该联系人");
			friend.get();
			refresh();
		})
		.catch((err) => {
			ui.showTips(err.message);
		});

	ui.hideLoading();
}

// 发消息
function send() {
	router.push({
		path: "/pages/chat/index",
		mode: "redirectTo",
		query: {
			userId: router.query.id,
		},
	});
}

// 删除
async function del() {
	ActionSheet.value?.open({
		list: [
			{
				label: `将联系人“${info.value.nickName}”删除，同时删除与该联系人的聊天记录`,
				disabled: true,
				size: "24rpx",
			},
			{
				label: "删除联系人",
				color: "red",
			},
		],
		callback(action) {
			if (action == 1) {
				service.chat.friend
					.delete({
						ids: [info.value.id],
					})
					.then(() => {
						ui.showTips("成功删除该联系人", () => {
							router.home();
						});
						friend.get();
					})
					.catch((err) => {
						ui.showTips(err.message);
					});
			}
		},
	});
}

onPullDownRefresh(async () => {
	await refresh();
	uni.stopPullDownRefresh();
});

onReady(() => {
	refresh();
});
